<template>
    <div id="movieMain">
      <!-- <div id="head">
        <div class="logo">
         <img src="./../assets/logo.png">
         <span>{{name}}</span>
        </div>
      </div> -->
      <div id="movie">
        <iframe :src="src"></iframe>
      </div>
    </div>
</template>

<script>
export default {
  mounted() {
    if (this.$route.params.url) {
      this.url = this.$route.params.url;
      this.name = this.$route.params.name;
      // this.src = "http://www.85105052.com/admin.php?url=" + this.url;
      this.src = "http://api.xyingyu.com/?url=" + this.url;
      // http://api.xyingyu.com/?url=
      // https://jx.kt111.top/jx/mf/?url=
      // https://api.pangujiexi.com/player.php?url=
      // http://api.lvcha2017.cn/?url=
      // http://kkk.2016ve.cn/kkjx/index.php?url=
      // http://mlxztz.com/vip/?url=
      // http://jy777.cn/XSD/XSD/?url=
    } else {
      // alert("直接打开的");
    }
  },
  data() {
    return {
      url: "",
      name: "",
      src: ""
    };
  },
  methods: {
    fullScreen() {
      var el = document.documentElement;
      var rfs =
        el.requestFullScreen ||
        el.webkitRequestFullScreen ||
        el.mozRequestFullScreen ||
        el.msRequestFullscreen;
      if (typeof rfs != "undefined" && rfs) {
        rfs.call(el);
      }
      return;
    },
    exitScreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
      if (typeof cfs != "undefined" && cfs) {
        cfs.call(el);
      }
    }
  }
};
</script>

<style lang="less">
#movieMain {
  background-color: #232325;
  width: 100%;
  height: 100%;
  color: #ffffff;
  #head {
    width: 100%;
    height: 60px;
    box-shadow: 0 0 3px #888;
    position: fixed;
    z-index: 1000;
    .logo {
      height: 60px;
      line-height: 60px;
      float: left;
      color: #ccc;
      margin-left: 20px;
      img {
        padding: 10px;
        width: 40px;
        height: 40px;
        vertical-align: middle;
        padding-right: 0;
        margin-right: 20px;
      }
    }
  }
  #movie {
    width: 98%;
    height: 100%;
    margin-left: 30px;
    iframe {
      width: 100%;
      height: 100%;
      border: none;
    }
  }
}
</style>



